.container {
  position: relative;
  width: 320px;
  height: 700px;
  margin: 50px auto 0 auto;
  perspective: 1000px;
  color: #fefefe;
  text-align: center;
}

.carousel {
  position: relative;
  margin-top: 200px;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  animation: rotate360 50s infinite forwards;
}

.carousel_fixed {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
}

.carousel__face {
  position: absolute;
  width: 300px;
  height: 187px;
  top: 20px;
  left: 10px;
  right: 10px;
  background-size: cover;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  cursor: pointer;
  font-size: 18px;
  transition: 0.4s ease-out;
}

.carousel__face:hover {
  transition: transform 0.4s;
  zoom: 1.1;
}

.carousel__face h3 {
  padding-top: 10px;
}

.carousel__face1 {
  background-color: #bb5657;
  transform: rotateY(0deg) translateZ(400px);
}
.carousel__face2 {
  background-color: #bb5657;
  transform: rotateY(72deg) translateZ(400px);
}
.carousel__face3 {
  background-color: #bb5657;
  transform: rotateY(144deg) translateZ(400px);
}
.carousel__face4 {
  background-color: #bb5657;
  transform: rotateY(216deg) translateZ(400px);
}
.carousel__face5 {
  background-color: #bb5658bd;
  transform: rotateY(288deg) translateZ(400px);
}

@keyframes rotate360 {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(-360deg);
  }
}

.img_carousel {
  width: 100%;
  height: 70%;
}
